@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@100&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  font-size: 1.1rem;
  font-family: "IBM Plex Sans", sans-serif;
}

body,
input,
button {
  font-family: Roboto, sans-serif;
}

.pageContainer {
  height: 100vh;
  display: flex;
}

.content {
  height: 100vh;
  flex: 1 1;
}

@media only screen and (max-width: 768px) {
  .content {
    height: 100vh;
    flex: 3 1;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
  cursor: pointer;
}

body {
  margin: 0;
  background-color: #f2f2f2;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1,
h2,
h3,
h4,
h5,
h6,
strong {
  font-weight: 500;
}

button {
  cursor: pointer;
}



header {
  padding-left: 100px;
  margin-bottom: 40px;
}

#header {
  margin-bottom: 40px;
}

#table {
  cursor: default;
  width: 100%;
  display: flex;
  justify-content: center !important;
}

th div:hover {
  cursor: default;
}

.error {
  font-size: 14px;
}
tr {
  cursor: pointer;
}
.bx--data-table-container {
  width: 100%;
  margin-bottom: 30px;
}
#click .bx--form-item {
  height: 48px !important;
  flex-direction: row !important;
}

.bx--file__selected-file {
  margin-right: 10px;
  margin-left: 0px;
  min-height: 48px;
  max-height: 48px;
  min-width: 180px;
  max-width: 180px;
}
.div-test td {
  max-height: 10px !important;
  padding: 0px !important;
}
#test- {
  width: 10px !important;
}
.resultados-table p {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 15px;

  padding-bottom: 5px;
}
.bx--parent-row .bx--expandable-row {
  font-family: "IBM Plex Sans", sans-serif;
  margin-left: 10px;
}
.bx--child-row-inner-container {
  /* border: 1px solid; */
  padding-bottom: 10px;
}

#expand {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 14px;
  margin-left: 108px;
  margin-top: 15px;
  padding-bottom: 10px;
}
tbody #test- {
  height: 70px;
}
.div-test .bx--table-expand {
  max-height: 10px !important;
  padding: 0px !important;
  width: 40px !important;
}
.div-test .bx--table-expand__button {
  margin-left: 15px;
  margin-bottom: 5px;
}
.div-test .bx--table-expand__svg {
  height: 20px;
}
.bx--btn--primary {
  width: 380px !important;
}
.bx--btn {
  max-width: none !important;
}
#textUpload {
  font-size: 15px;
  font-family: "IBM Plex Sans", sans-serif;
}
.bx--btn--primary {
  font-size: 15px;
  font-family: "IBM Plex Sans", sans-serif;
}
.bx--data-table-content {
  overflow: scroll;
  height: 200px;
}
.header-table {
  width: 980px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 8px;
  padding-bottom: 8px;
  background-color: #e0e0e0;
  font-weight: 550;
  cursor: default;
  height: 50px;
  align-items: center;
}
.titulo-table {
  padding-right: 1rem;
  padding-left: 1rem;
  text-align: left;
  font-size: 16px;
  width: 980px !important;
}
th {
  width: 327px !important;
}
td {
  width: 327px !important;
}
.bx--data-table-header__title {
  cursor: text;
  width: -webkit-fit-content !important;
  width: -moz-fit-content !important;
  width: fit-content !important;
  height: 10px;
}

.bx--file__drop-container{
    justify-content: flex-start !important;
    align-items: flex-start !important;
    height: 120px !important;
    width: 380px;
    font-size: 15px;
    font-family: 'IBM Plex Sans', sans-serif;
    padding-top: 12px !important;
    padding-left: 15px !important;
  }
.bx--file-browse-btn{
    margin-bottom: 0px !important;
    max-width: 380px !important;
}
  .bx--file--label{
    font-size: 20px !important;
  }
  .bx--label-description{
    font-size: 15px !important;
    margin-top: 10px;
  }
  #drop .bx--form-item{
    display: flex;
    justify-content: flex-start !important;
    align-items: flex-start !important;
    margin-bottom: 20px;
    width: 100% !important;
    

  }
  .bx--btn ~ .bx--file-container{
    margin-top: -75px;
    margin-left: 50px !important;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin-left: 30px;
    max-height: 120px !important;
    overflow: scroll;
  
  }
  
.fundo {
  height: 90vh;
  align-items: center;
  font-family: "IBM Plex Sans", sans-serif;
}
main {
  background-color: #f2f2f2;
  box-shadow: 0.1em 0.1em 0.3em rgb(209, 209, 209);
  height: 105%;
  display: flex;
  align-items: center;
  box-shadow: none;
  justify-content: center;
}

#div_Content {
  margin-top: 20px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-content: center;
  max-height: 280px;
}

#div_loader {
  border: 1px solid;
  justify-content: center;
  border: none;
  font-size: 1.2rem;
  color: #126dfb;
}
.resultados-table {
  overflow: scroll;
  max-height: 40px;
}

#load {
  margin-top: 10px;
}

#label {
  color: #126dfb;
}

.arrow {
  margin-top: 200px;
}

#conclusion {
  margin-top: 50px;
}

#campo_Entrada {
  display: flex;
  width: 100%;
  margin-bottom: 30px;
  flex-direction: row;
}

.icon {
  margin-left: 15px;
}
#arquivos {
  display: flex;
}
.container-principal {
  width: 75%;
  /* height: 100%; */
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 20px;
  background-color: white;
  padding-top: 20px;
  padding-bottom: 20px;
}

