#table {
  cursor: default;
  width: 100%;
  display: flex;
  justify-content: center !important;
}

th div:hover {
  cursor: default;
}

.error {
  font-size: 14px;
}
tr {
  cursor: pointer;
}
.bx--data-table-container {
  width: 100%;
  margin-bottom: 30px;
}
#click .bx--form-item {
  height: 48px !important;
  flex-direction: row !important;
}

.bx--file__selected-file {
  margin-right: 10px;
  margin-left: 0px;
  min-height: 48px;
  max-height: 48px;
  min-width: 180px;
  max-width: 180px;
}
.div-test td {
  max-height: 10px !important;
  padding: 0px !important;
}
#test- {
  width: 10px !important;
}
.resultados-table p {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 15px;

  padding-bottom: 5px;
}
.bx--parent-row .bx--expandable-row {
  font-family: "IBM Plex Sans", sans-serif;
  margin-left: 10px;
}
.bx--child-row-inner-container {
  /* border: 1px solid; */
  padding-bottom: 10px;
}

#expand {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 14px;
  margin-left: 108px;
  margin-top: 15px;
  padding-bottom: 10px;
}
tbody #test- {
  height: 70px;
}
.div-test .bx--table-expand {
  max-height: 10px !important;
  padding: 0px !important;
  width: 40px !important;
}
.div-test .bx--table-expand__button {
  margin-left: 15px;
  margin-bottom: 5px;
}
.div-test .bx--table-expand__svg {
  height: 20px;
}
.bx--btn--primary {
  width: 380px !important;
}
.bx--btn {
  max-width: none !important;
}
#textUpload {
  font-size: 15px;
  font-family: "IBM Plex Sans", sans-serif;
}
.bx--btn--primary {
  font-size: 15px;
  font-family: "IBM Plex Sans", sans-serif;
}
.bx--data-table-content {
  overflow: scroll;
  height: 200px;
}
.header-table {
  width: 980px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 8px;
  padding-bottom: 8px;
  background-color: #e0e0e0;
  font-weight: 550;
  cursor: default;
  height: 50px;
  align-items: center;
}
.titulo-table {
  padding-right: 1rem;
  padding-left: 1rem;
  text-align: left;
  font-size: 16px;
  width: 980px !important;
}
th {
  width: 327px !important;
}
td {
  width: 327px !important;
}
.bx--data-table-header__title {
  cursor: text;
  width: fit-content !important;
  height: 10px;
}
