@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@100&display=swap");

.fundo {
  height: 90vh;
  align-items: center;
  font-family: "IBM Plex Sans", sans-serif;
}
main {
  background-color: #f2f2f2;
  box-shadow: 0.1em 0.1em 0.3em rgb(209, 209, 209);
  height: 105%;
  display: flex;
  align-items: center;
  box-shadow: none;
  justify-content: center;
}

#div_Content {
  margin-top: 20px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-content: center;
  max-height: 280px;
}

#div_loader {
  border: 1px solid;
  justify-content: center;
  border: none;
  font-size: 1.2rem;
  color: #126dfb;
}
.resultados-table {
  overflow: scroll;
  max-height: 40px;
}

#load {
  margin-top: 10px;
}

#label {
  color: #126dfb;
}

.arrow {
  margin-top: 200px;
}

#conclusion {
  margin-top: 50px;
}

#campo_Entrada {
  display: flex;
  width: 100%;
  margin-bottom: 30px;
  flex-direction: row;
}

.icon {
  margin-left: 15px;
}
#arquivos {
  display: flex;
}
.container-principal {
  width: 75%;
  /* height: 100%; */
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 20px;
  background-color: white;
  padding-top: 20px;
  padding-bottom: 20px;
}
